import { CommonModule } from "@angular/common";
import { LoginComponent } from "./login/login.component";
import { RegisterComponent } from "./register/register.component";
import { GalleryModule } from "ng-gallery";
import { LightboxModule } from "ng-gallery/lightbox";
import { SharedModule } from "../shared/shared.module";
import { AuthRoutingModule } from "./auth-routing.module";
import { Injectable, NgModule } from "@angular/core";
import { AuthComponent } from "./auth.component";
import { AcountCreatedComponent } from "./account-created/account-created.component";
import { RegisterTallerComponent } from "./register-taller/register-taller.component";

import { NgxMaskDirective, NgxMaskPipe, provideNgxMask} from 'ngx-mask'
import { ForgetPasswordComponent } from "./forget-password/forget-password.component";
import { NgbDateParserFormatter, NgbDateStruct } from "@ng-bootstrap/ng-bootstrap";
import { LogoComponent } from "../home/widgets/logo/logo.component";
import { HomeModule } from "../home/home.module";
import { ResetPasswordComponent } from './reset-password/reset-password.component';
import { QRCodeModule } from 'angularx-qrcode';

/**
 * This Service handles how the date is rendered and parsed from keyboard i.e. in the bound input field.
*/
@Injectable()
export class CustomDateParserFormatter extends NgbDateParserFormatter {
	readonly DELIMITER = '/';

	parse(value: string): NgbDateStruct | null {
		if (value) {
			const date = value.split(this.DELIMITER);
			return {
				day: parseInt(date[0], 10),
				month: parseInt(date[1], 10),
				year: parseInt(date[2], 10),
			};
		}
		return null;
	}

	format(date: NgbDateStruct | null): string {
		return date ? date.day + this.DELIMITER + date.month + this.DELIMITER + date.year : '';
	}
}

@NgModule({
    declarations: [
      LoginComponent,
      RegisterComponent,
      AcountCreatedComponent,
      RegisterTallerComponent,
      ForgetPasswordComponent,
      ResetPasswordComponent,
    ],
    imports: [
      CommonModule,
      GalleryModule,
      LightboxModule,
      SharedModule,
      AuthRoutingModule,
      NgxMaskDirective, NgxMaskPipe, HomeModule, QRCodeModule
    ],
    providers: [provideNgxMask(), {provide: NgbDateParserFormatter, useClass: CustomDateParserFormatter }]
  })
  export class AuthModule { }
  