import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, NgForm, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { Subject, takeUntil } from 'rxjs';
import { AuthService } from 'src/app/core/auth/auth.service';

@Component({
  selector: "app-login",
  templateUrl: "./login.component.html",
  styleUrls: ["./login.component.scss"],
})
export class LoginComponent implements OnInit {

  private _unsubscribeAll: Subject<any> = new Subject<any>();

  @ViewChild("signInNgForm") signInNgForm: NgForm;

  signInForm: FormGroup;
  showAlert: boolean = false;
  messageAlert: string = "";
  activarClases: boolean = false;

  constructor(
    private _formBuilder: FormBuilder,
    private _authService: AuthService,
    private _router: Router
  ) {}

    // Logo
    public logo = [{
      image: 'assets/images/logos/1.png',
    }, {
      image: 'assets/images/logos/3.png',
    }, {
      image: 'assets/images/logos/2.png',
    }, {
      image: 'assets/images/logos/4.png',
    }, {
      image: 'assets/images/logos/5.png',
    }, {
      image: 'assets/images/logos/6.png',
    }, {
      image: 'assets/images/logos/7.png',
    },];

  ngOnInit(): void {
    // Create the form
    this.signInForm = this._formBuilder.group({
      email: ["", [Validators.required, Validators.email]],
      password: ["", Validators.required],
    });
  }

  /**
   * On destroy
   */
  ngOnDestroy(): void {
    // Unsubscribe from all subscriptions
    this._unsubscribeAll.next(null);
    this._unsubscribeAll.complete();
  }

  onSubmit(): void {
    this.showAlert = false;
    if(!this.signInForm.valid){
      this.activarClases = true;
      this.messageAlert = "Debes completar los campos para iniciar seson."
      this.showAlert = true;
      setTimeout(() => {
        this.showAlert = false
      }, 5000);
    }else{
      this.activarClases = false;
      this.signIn();
    }
  }

  // -----------------------------------------------------------------------------------------------------
  // @ Public methods
  // -----------------------------------------------------------------------------------------------------

  /**
   * Sign in
   */
  signIn(): void {
    // Return if the form is invalid
    if (this.signInForm.invalid) {
      return;
    }

    // Disable the form
    this.signInForm.disable();

    // Hide the alert
    this.showAlert = false;

    // Sign in
    this._authService.signIn(this.signInForm.value).pipe(takeUntil(this._unsubscribeAll))
    .subscribe(
            () => {
                // Set the redirect url.
                // The '/signed-in-redirect' is a dummy url to catch the request and redirect the user
                // to the correct page after a successful sign in. This way, that url can be set via
                // routing file and we don't have to touch here.
                var redirectURL:any;

                // return;

                if(this._authService.userRole === 'user'){
                  redirectURL = '/user/dashboard';
                }

                if(this._authService.userRole === 'taller'){
                  redirectURL = '/taller/dashboard';
                }

                if(this._authService.userRole === 'admin'){
                  redirectURL = '/pulseras/list';
                }
                // Navigate to the redirect url
                this._router.navigateByUrl(redirectURL);

            },
            (response) => {


                // Re-enable the form
                this.signInForm.enable();

                // Reset the form
                this.signInNgForm.resetForm();

                // Set the alert
                this.messageAlert = 'El email y la contrasea no coinciden'

                // Show the alert
                this.showAlert = true;

                setTimeout(() => {
                  this.showAlert = false
                }, 5000);
            }
        );
  }
}
