import { ChangeDetectorRef, Component, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { AbstractControl, FormBuilder, FormGroup, NgForm, ValidationErrors, ValidatorFn, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { Subject, finalize, takeUntil, takeWhile, tap, timer } from 'rxjs';
import { AuthService } from 'src/app/core/auth/auth.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-reset-password',
  templateUrl: './reset-password.component.html',
  styleUrls: ['./reset-password.component.scss']
})
export class ResetPasswordComponent implements OnInit, OnDestroy {

  private _unsubscribeAll: Subject<any> = new Subject<any>();

  @ViewChild("resetNgForm") resetNgForm: NgForm;

  Toast:any;

  token:any = '';

  resetPasswordForm: FormGroup;

  showAlert: boolean = false;
  messageAlert: string = "";
  activarClases: boolean = false;

  showSuccess:boolean =  false;

  countdown: number = 5;
  countdownMapping: any = {
      '=1'   : '# segundo',
      'other': '# segundos'
  };

  constructor(
    private _authService: AuthService,
    private _changeDetectorRef: ChangeDetectorRef,
    private route: ActivatedRoute,
    private _formBuilder: FormBuilder,
    private _router: Router
  ){
    this.Toast = Swal.mixin({
      toast: true,
      position: 'top-end',
      showConfirmButton: false,
      timer: 3000,
      timerProgressBar: true,
      didOpen: (toast) => {
        toast.addEventListener('mouseenter', Swal.stopTimer)
        toast.addEventListener('mouseleave', Swal.resumeTimer)
      }
    });  
  }

  ngOnInit(): void {
    this.route.queryParams.subscribe(params => {
        this.token = params.token;
      }
    );

    // Create the form
    this.resetPasswordForm = this._formBuilder.group({
        password: [
          "",
          Validators.compose([
            Validators.required,
            ResetPasswordComponent.patternValidator(/\d/, { hasNumber: true }),
            ResetPasswordComponent.patternValidator(/[A-Z]/, { hasCapitalCase: true }),
            ResetPasswordComponent.patternValidator(/[a-z]/, { hasSmallCase: true }),
            ResetPasswordComponent.patternValidator(
              /[!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?]/,
              { hasSpecialCharacters: true }
            ),
            Validators.minLength(8),
          ]),
        ],
      }
    );
  }

  /**
   * On destroy
   */
  ngOnDestroy(): void {
    // Unsubscribe from all subscriptions
    this._unsubscribeAll.next(null);
    this._unsubscribeAll.complete();
  }

  static patternValidator(regex: RegExp, error: ValidationErrors): ValidatorFn {
    return (control: AbstractControl): { [key: string]: any } => {
      if (!control.value) {
        // if control is empty return no error
        return null;
      }

      // test the value of the control against the regexp supplied
      const valid = regex.test(control.value);

      // if true, return no error (no error), else return error passed in the second parameter
      return valid ? null : error;
    };
  }

  // Logo
  public logo = [{
    image: 'assets/images/logos/1.png',
  }, {
    image: 'assets/images/logos/3.png',
  }, {
    image: 'assets/images/logos/2.png',
  }, {
    image: 'assets/images/logos/4.png',
  }, {
    image: 'assets/images/logos/5.png',
  }, {
    image: 'assets/images/logos/6.png',
  }, {
    image: 'assets/images/logos/7.png',
  }];

  resetPassword(){
    // Return if the form is invalid
    if ( this.resetPasswordForm.invalid )
    {
        return;
    }

    // Disable the form
    this.resetPasswordForm.disable();

    let data = {
      password: this.resetPasswordForm.get('password').value,
      token: this.token
    }

    this._authService.resetPassword(data).pipe(takeUntil(this._unsubscribeAll)).subscribe(
      (response:any) => {
        this.showSuccess = true;
        timer(1000, 1000).pipe(
          finalize(() => {
              this._router.navigate(['login']);
          }),
          takeWhile(() => this.countdown > 0),
          takeUntil(this._unsubscribeAll),
          tap(() => this.countdown--)
        )
        .subscribe();

      },(error) => {
        this.showSuccess = false;
        this.resetPasswordForm.enable();
        this.Toast.fire({
          icon: 'error',
          title: error.error.message
        });
      }
    );
  }

  onSubmit(){
    this.showAlert = false;
    if(!this.resetPasswordForm.valid){
      this.activarClases = true;
      this.messageAlert = "Debes completar el campo de la contrasea correctamente."
      this.showAlert = true;
      setTimeout(() => {
        this.showAlert = false
      }, 5000);
    }else{
      this.activarClases = false;
      this.resetPassword();
    }
  }

  prueba(){
    console.log("object");
    if(this.resetPasswordForm.valid){
      console.log("aqui");
    }
    this.resetPasswordForm.disable();
  }
}
